@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

#root {
    height: 100vh;
}

body {
    margin: 0;
    padding: 0;
    height: 100vh;
    font-family: 'Inter', sans-serif;
    font-optical-sizing: auto;
    font-style: normal;
    font-variation-settings: 'slnt' 0;
    font-weight: 700;
    background: linear-gradient(to bottom left, #67c493, 30%, #304358);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    scrollbar-width: thin;
    scrollbar-color: #ff8049 #e8e3e1;

    -webkit-scrollbar-width: thin;
    -webkit-scrollbar-color: #ff8049 #e8e3e1;
}

input,
button,
a {
    font-family: 'Inter', sans-serif;
    font-optical-sizing: auto;
    font-style: normal;
    font-variation-settings: 'slnt' 0;
    font-weight: 700;
    font-size: 16px;
}

table {
    width: 100%;
    border-collapse: collapse;
}

th, td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

th {
    background-color: #f2f2f2;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 800;
    margin: 0;
}

h1 {
    font-size: 24px;
}

h2 {
    font-size: 20px;
}

h3 {
    font-size: 14px;
    font-weight: 700;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}


